
import { defineComponent, ref } from 'vue';
import { InputGroups,InputField } from '@/interfaces/requestfields';


export default defineComponent({
  name: 'Request-Form',
  props: {
    groups: { type: Object as () => InputGroups, required: true}
  },

  computed: {
    getFlexBasis() {
      return (fields: InputField[]) => {
        const numItems = fields.length;
        return `calc(100% / ${numItems} - 12px)`;
      };
    },
  },
});
