import { createStore, ActionContext } from 'vuex'
import {Config} from "@/interfaces/config"
import {InputGroups} from "@/interfaces/requestfields"

interface MyState {
  config: Config
}

const state: MyState = {
  config: {
    donation_amount_cents: "",
    offer: "",
    npos: [],
    fields: {fields:[]}
  }
}


const actions = {
  async loadConfig({ commit }: ActionContext<MyState, any>) {
    const response = await fetch('data.json');
    const data = await response.json();
    commit('setConfig', data);
  }
}

const mutations = {
  setConfig(state: MyState, data: Config) {
    state.config = data;
  }
}

const getters = {
  getConfig: (state: MyState) => {
    return state.config
  }
}

export default createStore({
  state: state,
  getters: getters,
  mutations: mutations,
  actions: actions,
  modules: {
  }
})
